<template> 
  <div class="app-container">
    <el-card class="filter-container" shadow="never">
      <div>
        <i class="el-icon-search"></i>
        <span>筛选搜索</span>
        <el-button
          style="float:right"
          type="primary"
          @click="handleSearchList()"
          size="small">
          查询搜索
        </el-button>
        <el-button
          style="float:right;margin-right: 15px"
          @click="handleResetSearch()"
          size="small">
          重置
        </el-button>
      </div>
      <div style="margin-top: 15px">
        <el-form :inline="true" :model="listQuery" size="small" label-width="140px">
          <el-form-item label="输入搜索：">
            <el-input v-model="listQuery.orderId" class="input-width" placeholder="订单编号"></el-input>
          </el-form-item>
          <el-form-item label="收货人：">
            <el-input v-model="listQuery.receiverKeyword" class="input-width" placeholder="收货人姓名/手机号码"></el-input>
          </el-form-item>
          <el-form-item label="提交时间：">
            <el-date-picker
              class="input-width"
              v-model="listQuery.createTime"
              value-format="yyyy-MM-dd"
              type="date"
              placeholder="请选择时间">
            </el-date-picker>
          </el-form-item>
          <el-form-item label="订单状态：">
            <el-select v-model="listQuery.status" class="input-width" placeholder="全部" clearable>
              <el-option v-for="item in statusOptions"
                         :key="item.value"
                         :label="item.label"
                         :value="item.value">
              </el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="订单分类：">
            <el-select v-model="listQuery.orderType" class="input-width" placeholder="全部" clearable>
              <el-option v-for="item in orderTypeOptions"
                         :key="item.value"
                         :label="item.label"
                         :value="item.value">
              </el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="订单来源：">
            <el-select v-model="listQuery.sourceType" class="input-width" placeholder="全部" clearable>
              <el-option v-for="item in sourceTypeOptions"
                         :key="item.value"
                         :label="item.label"
                         :value="item.value">
              </el-option>
            </el-select>
          </el-form-item>
        </el-form>
      </div>
    </el-card>
    <el-card class="operate-container" shadow="never">
      <i class="el-icon-tickets"></i>
      <span>数据列表</span>
    </el-card>
    <div class="table-container">
      <el-table ref="orderTable"
                :data="list"
                style="width: 90%;overflow: auto;"
                @selection-change="handleSelectionChange"
                v-loading="listLoading" border>
        <el-table-column type="selection" width="60" align="center"></el-table-column>
        <el-table-column label="编号" width="80" align="center">
          <template slot-scope="scope">{{scope.row.orderId}}</template>
        </el-table-column>
        <el-table-column label="订单编号" width="180" align="center">
          <template slot-scope="scope">{{scope.row.orderSn}}</template>
        </el-table-column>
        <el-table-column label="提交时间" width="180" align="center">
          <template slot-scope="scope">{{scope.row.createdDate | formatCreateTime}}</template>
        </el-table-column>
        <el-table-column label="订单用户ID" align="center">
          <template slot-scope="scope">{{scope.row.userId}}</template>
        </el-table-column>
        <el-table-column label="订单金额" width="120" align="center">
          <template slot-scope="scope">￥{{scope.row.orderAmount}}</template>
        </el-table-column>
        <el-table-column label="物流单号" width="180" align="center">
          <template slot-scope="scope">{{scope.row.expressCode}}</template>
        </el-table-column>
        <el-table-column label="物流渠道" width="120" align="center">
          <template slot-scope="scope">{{scope.row.expressCompany}}</template>
        </el-table-column>
        <el-table-column label="订单状态" width="120" align="center">
          <template slot-scope="scope">{{scope.row.status | formatStatus}}</template>
        </el-table-column>
        <el-table-column label="操作" width="200" align="center">
          <template slot-scope="scope">
            <el-button
              size="mini"
              @click="handleViewOrder(scope.$index, scope.row)"
            >查看订单
            </el-button>
            <el-button
              size="mini"
              @click="handleCloseOrder(scope.$index, scope.row)"
              v-show="scope.row.status===0">关闭订单
            </el-button>
            <el-button
              size="mini"
              @click="handleDeliveryOrder(scope.$index, scope.row)"
              v-show="scope.row.status===2">订单发货
            </el-button>
            <el-button
              size="mini"
              @click="handleViewLogistics(scope.$index, scope.row)"
              v-show="scope.row.status===2||scope.row.status===3">订单跟踪
            </el-button>
            <el-button
              size="mini"
              type="danger"
              @click="handleDeleteOrder(scope.$index, scope.row)"
              v-show="scope.row.status===4">删除订单
            </el-button>
          </template>
        </el-table-column>
      </el-table>
    </div>
    <div class="batch-operate-container">
      <el-select
        size="small"
        v-model="operateType" placeholder="批量操作">
        <el-option
          v-for="item in operateOptions"
          :key="item.value"
          :label="item.label"
          :value="item.value">
        </el-option>
      </el-select>
      <el-button
        style="margin-left: 20px"
        class="search-button"
        @click="handleBatchOperate()"
        type="primary"
        size="small">
        确定
      </el-button>
    </div>
    <div class="pagination-container">
      <el-pagination
        background
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        layout="total, sizes,prev, pager, next,jumper"
        :current-page.sync="listQuery.pageNum"
        :page-size="listQuery.pageSize"
        :page-sizes="[5,10,15]"
        :total="total">
      </el-pagination>
    </div>
    <el-dialog
      title="关闭订单"
      :visible.sync="closeOrder.dialogVisible" width="30%">
      <span style="vertical-align: top">操作备注：</span>
      <el-input
        style="width: 80%"
        type="textarea"
        :rows="5"
        placeholder="请输入内容"
        v-model="closeOrder.content">
      </el-input>
      <span slot="footer" class="dialog-footer">
        <el-button @click="closeOrder.dialogVisible = false">取 消</el-button>
        <el-button type="primary" @click="handleCloseOrderConfirm">确 定</el-button>
      </span>
    </el-dialog>
    <logistics-dialog v-model="logisticsDialogVisible"></logistics-dialog>
  </div>
</template>
<script>
    import {fetchList, closeOrder, deleteOrder,batchDeleteOrder} from '@/api/order'
    import {formatDate} from '@/utils/date';
    import LogisticsDialog from '@/views/oms/order/components/logisticsDialog';

    const defaultListQuery = {
        pageNum: 1,
        pageSize: 10,
        orderSn: null,
        receiverKeyword: null,
        status: null,
        orderType: null,
        sourceType: null,
        createTime: null,
    };
    export default {
        name: "orderList",
        components: {LogisticsDialog},
        data() {
            return {
                listQuery: Object.assign({}, defaultListQuery),
                listLoading: true,
                list: null,
                total: null,
                operateType: null,
                multipleSelection: [],
                closeOrder: {
                    dialogVisible: false,
                    content: null,
                    orderIds: []
                },
                statusOptions: [

                    {
                        label: '待付款',
                        value: 1
                    },
                    {
                        label: '待发货',
                        value: 2
                    },
                    {
                        label: '待收货',
                        value: 3
                    },
                    {
                        label: '待评价',
                        value: 4
                    },
                    {
                        label: '交易完成',
                        value: 5
                    }
                    // ,
                    // {
                    //     label: '已退款',
                    //     value: 203
                    // },
                    // {
                    //     label: '已发货',
                    //     value: 301
                    // },
                    // {
                    //     label: '用户收货',
                    //     value: 401
                    // },
                    // {
                    //     label: '系统收货',
                    //     value: 402
                    // }
                ],
                orderTypeOptions: [
                    {
                        label: '正常订单',
                        value: 1
                    },
                    {
                        label: '积分订单',
                        value: 2
                    }
                ],
                sourceTypeOptions: [
                    {
                        label: '抢购',
                        value: 2
                    },
                    {
                        label: '拼团',
                        value: 3
                    }
                ],
                operateOptions: [
                    {
                        label: "批量发货",
                        value: 1
                    },
                    {
                        label: "关闭订单",
                        value: 2
                    },
                    {
                        label: "删除订单",
                        value: 3
                    }
                ],
                logisticsDialogVisible: false
            }
        },
        created() {
            this.getList();
        },
        filters: {
            formatCreateTime(time) {
                let date = new Date(time);
                return formatDate(date, 'yyyy-MM-dd hh:mm:ss')
            },
            formatPayType(value) {
                if (value === 1) {
                    return '支付宝';
                } else if (value === 2) {
                    return '微信';
                } else {
                    return '未支付';
                }
            },
            formatSourceType(value) {
                if (value === 2) {
                    return '抢购';
                } else if(value === 3) {
                    return '拼团';
                }else{
                    return '普通';
                }
            },
            formatStatus(value) {
                if (value === 1) {
                    return '待付款';
                } else if (value === 2) {
                    return '待发货';
                } else if (value === 3) {
                    return '待收货';
                } else if (value === 4) {
                    return '待评价';
                } else if (value === 5) {
                    return '交易完成';
                }
                // else if(value === 203){
                //     return '已退款';
                // } else if(value === 301){
                //     return '已发货';
                // } else if(value === 401){
                //     return '用户收货';
                // } else if(value === 402){
                //     return '系统收货';
                // }
            },
        },
        methods: {
            handleResetSearch() {
                this.listQuery = Object.assign({}, defaultListQuery);
            },
            handleSearchList() {
                this.listQuery.pageNum = 1;
                this.getList();
            },
            handleSelectionChange(val) {
                this.multipleSelection = val;
            },
            handleViewOrder(index, row) {
                this.$router.push({path: '/oms/orderDetail', query: {id: row.orderId}})
            },
            handleCloseOrder(index, row) {
                this.closeOrder.dialogVisible = true;
                this.closeOrder.orderIds = [row.id];
            },
            handleDeliveryOrder(index, row) {
              console.log(row);
                let listItem = this.covertOrder(row);
                this.$router.push({path: '/oms/deliverOrderList', query: {list: [listItem]}})
            },
            handleViewLogistics(index, row) {
                this.logisticsDialogVisible = true;
            },
            handleDeleteOrder(index, row) {
                let ids = [];
                ids.push(row.orderId);
                this.deleteOrder(ids);
            },
            handleBatchOperate() {
                if (this.multipleSelection == null || this.multipleSelection.length < 1) {
                    this.$message({
                        message: '请选择要操作的订单',
                        type: 'warning',
                        duration: 1000
                    });
                    return;
                }
                if (this.operateType === 1) {
                    /*//批量发货
                    let list = [];
                    for (let i = 0; i < this.multipleSelection.length; i++) {
                        if (this.multipleSelection[i].status === 1) {
                            list.push(this.covertOrder(this.multipleSelection[i]));
                        }
                    }
                    if (list.length === 0) {
                        this.$message({
                            message: '选中订单中没有可以发货的订单',
                            type: 'warning',
                            duration: 1000
                        });
                        return;
                    }
                    this.$router.push({path: '/oms/deliverOrderList', query: {list: list}})*/
                    //批量删除
                    this.$confirm('是否要进行发货操作?', '提示', {
                        confirmButtonText: '确定',
                        cancelButtonText: '取消',
                        type: 'warning'
                    }).then(() => {
                        let params = new URLSearchParams();
                        let ids=[];
                        for(let i=0;i<this.multipleSelection.length;i++){
                            ids.push(this.multipleSelection[i].orderId);
                        }
                        params.append("ids",ids);
                        params.append("type","3");
                        batchDeleteOrder(params).then(response=>{
                            this.getList();
                            this.$message({
                                type: 'success',
                                message: '发货成功!'
                            });
                        });
                    })
                } else if (this.operateType === 2) {
                    /*//关闭订单
                    this.closeOrder.orderIds = [];
                    for (let i = 0; i < this.multipleSelection.length; i++) {
                        this.closeOrder.orderIds.push(this.multipleSelection[i].id);
                    }
                    this.closeOrder.dialogVisible = true;*/
                    //批量删除
                    this.$confirm('是否要进行关闭订单操作?', '提示', {
                        confirmButtonText: '确定',
                        cancelButtonText: '取消',
                        type: 'warning'
                    }).then(() => {
                        let params = new URLSearchParams();
                        let ids=[];
                        for(let i=0;i<this.multipleSelection.length;i++){
                            ids.push(this.multipleSelection[i].orderId);
                        }
                        params.append("ids",ids);
                        params.append("type","5");
                        batchDeleteOrder(params).then(response=>{
                            this.getList();
                            this.$message({
                                type: 'success',
                                message: '关闭成功!'
                            });
                        });
                    })
                } else if (this.operateType === 3) {
                    /*//删除订单
                    let ids = [];
                    for (let i = 0; i < this.multipleSelection.length; i++) {
                        ids.push(this.multipleSelection[i].id);
                    }
                    this.deleteOrder(ids);*/
                    //批量删除
                    this.$confirm('是否要进行删除操作?', '提示', {
                        confirmButtonText: '确定',
                        cancelButtonText: '取消',
                        type: 'warning'
                    }).then(() => {
                        let params = new URLSearchParams();
                        let ids=[];
                        for(let i=0;i<this.multipleSelection.length;i++){
                            ids.push(this.multipleSelection[i].orderId);
                        }
                        params.append("ids",ids);
                        params.append("type","2");
                        batchDeleteOrder(params).then(response=>{
                            this.getList();
                            this.$message({
                                type: 'success',
                                message: '删除成功!'
                            });
                        });
                    })
                }
            },
            handleSizeChange(val) {
                this.listQuery.pageNum = 1;
                this.listQuery.pageSize = val;
                this.getList();
            },
            handleCurrentChange(val) {
                this.listQuery.pageNum = val;
                this.getList();
            },
            handleCloseOrderConfirm() {
                if (this.closeOrder.content == null || this.closeOrder.content === '') {
                    this.$message({
                        message: '操作备注不能为空',
                        type: 'warning',
                        duration: 1000
                    });
                    return;
                }
                let params = new URLSearchParams();
                params.append('ids', this.closeOrder.orderIds);
                params.append('note', this.closeOrder.content);
                closeOrder(params).then(response => {
                    this.closeOrder.orderIds = [];
                    this.closeOrder.dialogVisible = false;
                    this.getList();
                    this.$message({
                        message: '修改成功',
                        type: 'success',
                        duration: 1000
                    });
                });
            },
            getList() {
                this.listLoading = true;
                fetchList(this.listQuery).then(response => {
                    this.listLoading = false;
                    this.list = response.data;
                    this.total = response.total;
                });
            },
            deleteOrder(ids) {
                this.$confirm('是否要进行该删除操作?', '提示', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    type: 'warning'
                }).then(() => {
                    let params = new URLSearchParams();
                    params.append("orderId", ids);
                    deleteOrder(params).then(response => {
                        this.$message({
                            message: '删除成功！',
                            type: 'success',
                            duration: 1000
                        });
                        this.getList();
                    });
                })
            },
            covertOrder(order) {
                let address = order.receiverProvince + order.receiverCity + order.receiverRegion + order.receiverDetailAddress;
                let listItem = {
                    orderId: order.orderId,
                    orderSn: order.orderSn,
                    receiverName: order.receiverName,
                    receiverPhone: order.addresseePhone,
                    receiverPostCode: order.receiverPostCode,
                    address: order.addressee,
                    deliveryCompany: null,
                    deliverySn: null
                };
                return listItem;
            }
        }
    }
</script>
<style scoped>
  .input-width {
    width: 203px;
  }
</style>





